// Define a drawing function
export const drawRect = (boxes, classes, scores, threshold, imgWidth, imgHeight, ctx)=>{
    for(let i=0; i<=boxes.length; i++){
        if(boxes[i] && classes[i] && scores[i]>threshold){
            // Extract variables
            const [y,x,height,width] = boxes[i]
            const text = classes[i]
          
            // Set styling
            ctx.strokeStyle = labelMap[text]['color']
            ctx.lineWidth = 10
            ctx.fillStyle = 'white'
            ctx.font = '30px Arial'         
            
            // DRAW!!
            ctx.beginPath()
            ctx.fillText(labelMap[text]['name'] + ' - ' + Math.round(scores[i]*100)/100, x*imgWidth, y*imgHeight-10)
            ctx.rect(x*imgWidth, y*imgHeight, width*imgWidth/2, height*imgHeight/1.5);
            ctx.stroke()

            var textname = labelMap[text]['name'];
            console.log(textname);
            
         //   document.getElementById('video1').addEventListener('ended',myHandler,false);
          //  function myHandler(e) {
          //      video1.src = "3.mp4";
           //     video3.src = "1.mp4";
          //  }    
           
//video functionality
         if (textname.includes('On') === true) {
                    ActivateCall(); 
                 //   video2.play(); 
                //    video3.play();
             //  video1.play();
                    executedForOff = false;

              } 
              else if (textname.includes('No') === true){
                //    video3.pause();
                    endcall();
                    CallRingtone.pause();        
                    executedForOn = false;
                    executedForCall = false;
              };
        }
    }
}

